</template>

<script>
    import MyFooter from '../../components/my-footer/my-footer';
    import {detail} from "../../api/users";
    import {SUCCESS,IMGHOSTNAME} from "../../config/base";
    import {Toast} from 'vant'
    export default {
        name: "my",
        components:{
          MyFooter
        },
        data(){
            return {
                users:null,
                imgHostname: IMGHOSTNAME
            }
        },
        methods:{
            getone(){
                detail('/api/users/1').then(res=>{
                    let {code,msg,data}=res;
                    if (code == SUCCESS){
                        this.users = data;
                    } else{
                        Toast(msg)
                    }
                });
            },
            Cancellation(){
                localStorage.removeItem('vuex');
                this.$router.push({name:'index'});
            }
        },
        beforeMount(){
            this.getone();
        }
    }
</script>

<style scoped>
    @import "../../style/css/my.css";
